<template>
    <header class="header">
        <h1 class="page-title" tabindex="0" @click="showMainMode">Monitor sieci ZigBee</h1>
        <nav>
            <template v-if="isViewMode">
                <button type="button" class="header-button" @click="refresh">Odśwież</button>
                <button type="button" class="header-button" @click="editNewLayer">Dodaj mapę</button>
                <button type="button" class="header-button" @click="editActiveLayer">Edytuj mapę</button>
                <button type="button" class="header-button" @click="deleteActiveLayer">Usuń mapę</button>
                <button v-if="isAdmin" type="button" class="header-button" @click="manageUsers">Użytkownicy</button>
            </template>
            <button v-if="isUsersManagement" type="button" class="header-button" @click="showMainMode">Mapa</button>
            <template v-if="isLoggedIn">
                <dropdown-menu>
                    <template v-slot:toggle-button>
                        <img src="~@/assets/icons/person.svg" class="user-icon" />
                        <span class="button-username">{{ user ? user.username : '' }}</span>
                    </template>
                    <template v-slot:content>
                        <button type="button" class="dropdown-list-button" @click="changePassword">Zmień hasło</button>
                        <button type="button" class="dropdown-list-button" @click="logout">Wyloguj</button>
                    </template>
                </dropdown-menu>
            </template>
        </nav>
    </header>
</template>

<script>

import DropdownMenu from './DropdownMenu.vue';

export default {
    name:"AppHeader",
    components:{
        DropdownMenu
    },
    data(){
        return{
        }
    },
    computed:{
        isViewMode(){
            return this.$store.getters.mode === 'view';
        },
        isUsersManagement(){
            const userManagementModes = ['changePassword', 'manageUsers', 'showUser', 'newUser', 'editUser']
            return userManagementModes.includes(this.$store.getters.mode);
        },
        isLoggedIn(){
            return this.$store.state.user !== null;
        },
        isAdmin(){
            return this.user &&  this.user.role === 'admin';
        },
        user(){
            return this.$store.state.user;
        }
    },
    methods:{
        showMainMode(){
            this.$store.commit('pushMode', 'view');
        },
        editNewLayer(){
            this.$store.commit('prepareNewLayer');
            this.$store.commit('pushMode', 'newLayer');
        },
        editActiveLayer(){
            this.$store.commit('prepareLayerForEdit', this.$store.getters.activeLayer);
            this.$store.commit('pushMode', 'editLayer');
        },
        async deleteActiveLayer(){
            await this.$store.dispatch('deleteLayer', this.$store.getters.activeLayer);
        },
        async refresh(e){
            try{
                e.target.disabled = true;
                await this.$store.dispatch('downloadDiscoveryResults');
            }
            finally{
                e.target.disabled = false;
            }
        },
        async logout(){
            try{
                await this.$store.dispatch('logout');
                this.$store.commit('replaceMode', 'login');
            }
            catch(e){
                console.log(e);
            }
        },
        changePassword(){
            this.$store.commit('pushMode', 'changePassword');
        },
        manageUsers(){
            this.$store.commit('pushMode', 'manageUsers');
        }
    },
}
</script>

<style scoped>

.header{
    flex:none;
    border-bottom:1px solid #E6E6FA;
    box-sizing:border-box;
    display:flex;
    align-items: center;
    justify-content: space-between;
}

.page-title{
    font-size:20px;
    font-weight:normal;
    margin: 10px;
    cursor: pointer;
}

.user-icon{
    width: 1.3em;
    height: 1.3em;
    vertical-align: -0.4em;
    display:inline-block;
    margin-right:5px;
}

.button-username{
    font-weight:600;
}


</style>